import React from "react";
import { Button, Card, Table, Container, Col, Row } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { Card1 } from "../../components/molecules";
import { Card4 } from "../../components/atoms";
import "./Skills.css";
import {
  IcHtml,
  IcReact,
  IcAi,
  IcCss,
  IcBoot,
  IcFigma,
  IcPs,
  IcJs
} from "../../assests";

const Home = () => {
  return (
    <>
      <div id="Skills">
        <div className="py-5 bg-cont " style={{ backgroundColor: "#1E2880" }}>
          <Container>
            <h1 className="text-center mt-5 mb-3 font-mulish-bold text-white">
              My Skills
            </h1>
            <Row>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="HTML" value={90} icons={IcHtml} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="CSS" value={90} icons={IcCss} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="Javascript" value={75} icons={IcJs} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="React/Native" value={80} icons={IcReact} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="Figma" value={95} icons={IcFigma} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="Adobe Illustrator" value={80} icons={IcAi} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="Phototshop" value={80} icons={IcPs} />
              </Col>
              <Col
                sm={6}
                md={4}
                lg={3}
                className="d-flex justify-content-center"
              >
                <Card4 myskill="Bootstrap" value={80} icons={IcBoot} />
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </>
  );
};

export default Home;
