import React from "react";
import HoverCard from "react-png-hovercard";
import "./module.css";

const Card4 = ({ myskill, value, icons }) => {
  function MouseOver(event) {
    event.target.style.background = "red";
  }
  function MouseOut(event) {
    event.target.style.background = "";
  }

  return (
    <>
      <HoverCard
        maxWidth={200}
        animationSpeed={100}
        height={200}
        margin={20}
        front={
          <div className="Front">
            <img src={icons} alt="" style={{ objectFit: "cover" }} />
          </div>
        }
        back={
          <div className="Front">
            <div>
              <h6 className="text-center"> {myskill}</h6>
              <progress id="file" value={value} max="100" />
            </div>
          </div>
        }
      />
    </>
  );
};

export default Card4;
