import React from "react";
import { Button, Card, Table, Container, Col, Row } from "react-bootstrap";
import { birthday } from "../../../assests";
import "./card2.css";

const Card2 = ({ icons, desc }) => {
  return (
    <div>
      <div className="d-flex  border rounded bg-light border-primary mb-3 shadow ">
        <div className="p-2 w-20 align-self-center ">
          <img src={icons} />
        </div>
        <div className="p-2  w-80 align-self-center">
          <h5 className="font-mulish text-secondary">{desc}</h5>
        </div>
      </div>
    </div>
  );
};

export default Card2;
