import React from "react";
import { Navbar, Container, Nav, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { Logo, Menus, Logos } from "../../../assests";
import { Link } from "react-router-dom";

const Navbar2 = () => {
  return (
    <div className="fixed-top  d-sm-none d-md-block d-none d-sm-block">
      <Navbar
        collapseOnSelect
        expand="lg"
        variant="dark"
        style={{ backgroundColor: "#1A1E3E", boxShadow: "1px 2px 1px #151831" }}
      >
        <Container>
          <Navbar.Brand href="#About">
            {" "}
            <img src={Logos} />{" "}
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav">
            <img src={Menus} />
          </Navbar.Toggle>
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#About">Home</Nav.Link>
              <Nav.Link href="#Info">About</Nav.Link>
              <Nav.Link href="#Skills">Skills</Nav.Link>
              <Nav.Link href="#Contact">Contact</Nav.Link>
            </Nav>
            <Nav>
              <Button
                style={{ backgroundColor: "#00FFF0", color: "black" }}
                href="mailto:imammlydi892@gmail.com"
              >
                Messages
              </Button>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};

export default Navbar2;
