import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import ExploreSharpIcon from "@material-ui/icons/ExploreSharp";
import ExploreOutlinedIcon from "@material-ui/icons/ExploreOutlined";
import PersonSharpIcon from "@material-ui/icons/PersonSharp";
import HomeRoundedIcon from "@material-ui/icons/HomeRounded";
import PermDeviceInformationIcon from "@material-ui/icons/PermDeviceInformation";

const useStyles = makeStyles({
  root: {
    width: "100%",
    backgroundColor: "#FFFFF"
  }
});

const Navbar3 = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  return (
    <div className="border classes.root fixed-bottom  d-xxl-none d-xl-none d-xxl-block d-lg-none d-xl-block d-md-none d-lg-block">
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        showLabels
        className={classes.root}
      >
        <BottomNavigationAction
          // label="Home"
          icon={<HomeRoundedIcon />}
          href="#About"
        />
        <BottomNavigationAction
          // label="Profile"
          icon={<PersonSharpIcon />}
          href="#Info"
        />
        <BottomNavigationAction
          // label="Skills"
          icon={<ExploreSharpIcon />}
          href="#Skills"
        />
        <BottomNavigationAction
          // label="Skills"
          icon={<PermDeviceInformationIcon />}
          href="#Contact"
        />
      </BottomNavigation>
    </div>
  );
};

export default Navbar3;
