import React from "react";
import { Button, Card, Table, Container, Col, Row } from "react-bootstrap";
import "./info.css";
import {
  birthday,
  alamat,
  education,
  WA,
  gym,
  video,
  Music,
  Illust2,
  Illust3
} from "../../assests";
import Card2 from "../../components/atoms/Card2";
import Card4 from "../../components/atoms/Card4";

const Info = () => {
  return (
    <>
      <div>
        <div className="py-5 bg-con" id="Info">
          <Container>
            <Row>
              <Col md={6} lg={6}>
                <h2 className="mt-5 text-center font-mulish-bold text-dark mb-5">
                  About Me
                </h2>
                <Card2 icons={birthday} desc="08 September 1992" />
                <Card2 icons={alamat} desc="Jakarta" />
                <Card2 icons={education} desc="Smk Muhammadiya Lemahabang" />
                <Card2 icons={WA} desc="081297137109" />
                <Card2 icons={gym} desc="Sport, running" />
                <Card2 icons={Music} desc="Listening to music" />
                <Card2 icons={video} desc=" Watch Film and youtube tutorials" />
              </Col>
              <Col sm={6} lg={6}>
                <div className=" d-flex justify-content-center mt-5 d-sm-none d-md-block d-none d-sm-block d-md-none d-lg-block">
                  <img src={Illust3} className="" />
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </>
  );
};

export default Info;
